'use client';

import { useState, useEffect } from 'react';
import { FiEdit2, FiTrash2, FiChevronDown, FiChevronRight, FiPlus } from 'react-icons/fi';
import { db } from '../../config/firebase';
import { collection, getDocs, deleteDoc, doc, query, where, addDoc, updateDoc } from 'firebase/firestore';
import { v4 as uuidv4 } from 'uuid';

interface Namespace {
  id: string;
  'namespace-name': string;
  'namespace-url': string;
}

interface NamespaceAccount {
  id: string;
  'namespace-id': string;
  'namespace-name': string;
  'namespace-account-url-override': string;
}

interface NamespaceMethod {
  id: string;
  'namespace-id': string;
  'namespace-account-method-name': string;
  'namespace-account-method-url-override': string;
}

interface AddAccountFormProps {
  namespaceId: string;
  namespaceName: string;
  onClose: () => void;
  onAdd: () => void;
}

interface AddMethodFormProps {
  namespaceId: string;
  onClose: () => void;
  onAdd: () => void;
}

interface EditAccountFormProps extends AddAccountFormProps {
  editData: NamespaceAccount;
}

interface EditMethodFormProps extends AddMethodFormProps {
  editData: NamespaceMethod;
}

function AddAccountForm({ namespaceId, namespaceName, onClose, onAdd }: AddAccountFormProps) {
  const [accountName, setAccountName] = useState('');
  const [urlOverride, setUrlOverride] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      await addDoc(collection(db, 'namespace-account'), {
        'namespace-id': namespaceId,
        'namespace-name': accountName,
        'namespace-account-id': uuidv4(),
        'namespace-account-url-override': urlOverride,
        'namespace-account-header': []
      });
      onAdd();
      onClose();
    } catch (error) {
      console.error('Error adding account:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 bg-white p-4 rounded-lg border">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Account Name
        </label>
        <input
          type="text"
          value={accountName}
          onChange={(e) => setAccountName(e.target.value)}
          className="w-full px-3 py-2 border rounded"
          required
        />
      </div>
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          URL Override
        </label>
        <input
          type="text"
          value={urlOverride}
          onChange={(e) => setUrlOverride(e.target.value)}
          className="w-full px-3 py-2 border rounded"
        />
      </div>
      <div className="flex justify-end gap-2">
        <button
          type="button"
          onClick={onClose}
          className="px-4 py-2 text-gray-600 hover:text-gray-800"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={loading}
          className="px-4 py-2 bg-[#ff6b4a] text-white rounded hover:bg-[#ff5436] disabled:opacity-50"
        >
          {loading ? 'Adding...' : 'Add Account'}
        </button>
      </div>
    </form>
  );
}

function AddMethodForm({ namespaceId, onClose, onAdd }: AddMethodFormProps) {
  const [methodName, setMethodName] = useState('');
  const [urlOverride, setUrlOverride] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      await addDoc(collection(db, 'namespace-account-method'), {
        'namespace-id': namespaceId,
        'namespace-account-method-name': methodName,
        'namespace-account-method-id': uuidv4(),
        'namespace-account-method-url-override': urlOverride,
        'namespace-account-method-queryParams': []
      });
      onAdd();
      onClose();
    } catch (error) {
      console.error('Error adding method:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 bg-white p-4 rounded-lg border">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Method Name
        </label>
        <input
          type="text"
          value={methodName}
          onChange={(e) => setMethodName(e.target.value)}
          className="w-full px-3 py-2 border rounded"
          required
        />
      </div>
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          URL Override
        </label>
        <input
          type="text"
          value={urlOverride}
          onChange={(e) => setUrlOverride(e.target.value)}
          className="w-full px-3 py-2 border rounded"
        />
      </div>
      <div className="flex justify-end gap-2">
        <button
          type="button"
          onClick={onClose}
          className="px-4 py-2 text-gray-600 hover:text-gray-800"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={loading}
          className="px-4 py-2 bg-[#ff6b4a] text-white rounded hover:bg-[#ff5436] disabled:opacity-50"
        >
          {loading ? 'Adding...' : 'Add Method'}
        </button>
      </div>
    </form>
  );
}

function EditAccountForm({ namespaceId, editData, onClose, onAdd }: EditAccountFormProps) {
  const [accountName, setAccountName] = useState(editData['namespace-name']);
  const [urlOverride, setUrlOverride] = useState(editData['namespace-account-url-override']);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      await updateDoc(doc(db, 'namespace-account', editData.id), {
        'namespace-name': accountName,
        'namespace-account-url-override': urlOverride,
      });
      onAdd();
      onClose();
    } catch (error) {
      console.error('Error updating account:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 bg-white p-4 rounded-lg border">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Account Name
        </label>
        <input
          type="text"
          value={accountName}
          onChange={(e) => setAccountName(e.target.value)}
          className="w-full px-3 py-2 border rounded"
          required
        />
      </div>
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          URL Override
        </label>
        <input
          type="text"
          value={urlOverride}
          onChange={(e) => setUrlOverride(e.target.value)}
          className="w-full px-3 py-2 border rounded"
        />
      </div>
      <div className="flex justify-end gap-2">
        <button type="button" onClick={onClose}>Cancel</button>
        <button type="submit" disabled={loading}>
          {loading ? 'Saving...' : 'Save Changes'}
        </button>
      </div>
    </form>
  );
}

